"use client";

import { useState } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Progress } from "@/components/ui/progress";
import { Textarea } from "@/components/ui/textarea";

const tenantSchema = z.object({
  name: z.string().min(1, "El nombre es requerido"),
  email: z.string().email("Email inválido"),
  phone: z.string().min(10, "Teléfono debe tener al menos 10 dígitos"),
  income: z.number().min(0, "Los ingresos deben ser positivos"),
  occupation: z.string().min(1, "La ocupación es requerida"),
  maxRent: z.number().min(0, "El alquiler máximo debe ser positivo"),
  preferredAreas: z.string().min(1, "Indique al menos una zona preferida"),
  petFriendly: z.boolean().optional(),
  smokingAllowed: z.boolean().optional(),
});

type TenantFormData = z.infer<typeof tenantSchema>;

export default function TenantRegisterPage() {
  const [step, setStep] = useState(1);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitError, setSubmitError] = useState<string | null>(null);
  const [submitSuccess, setSubmitSuccess] = useState<string | null>(null);
  const [tenantId, setTenantId] = useState<string | null>(null);
  const [verificationStatus, setVerificationStatus] = useState<'pending' | 'verifying' | 'success' | 'failed'>('pending');

  const { register, handleSubmit, formState: { errors }, watch, setValue } = useForm<TenantFormData>({
    resolver: zodResolver(tenantSchema),
    defaultValues: {
      petFriendly: false,
      smokingAllowed: false,
    }
  });

  const watchedValues = watch();

  const onSubmit = async (data: TenantFormData) => {
    setIsSubmitting(true);
    setSubmitError(null);
    setSubmitSuccess(null);

    try {
      // Step 1: Register tenant
      const registerResponse = await fetch('/api/tenant/register', {
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          ...data,
          documents: [], // Will be handled in file upload step
          biometricData: null // Will be handled in verification step
        }),
      });

      const registerResult = await registerResponse.json();

      if (!registerResponse.ok) {
        throw new Error(registerResult.error || "Error en el registro");
      }

      setTenantId(registerResult.tenantId);
      setSubmitSuccess(registerResult.message);
      setStep(2); // Move to verification step

    } catch (error) {
      console.error('Registration error:', error);
      setSubmitError(error instanceof Error ? error.message : "Error desconocido");
    } finally {
      setIsSubmitting(false);
    }
  };

  const handleVerification = async () => {
    if (!tenantId) return;

    setVerificationStatus('verifying');
    setSubmitError(null);

    try {
      // Simulate biometric data collection
      const mockBiometricData = `biometric_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;

      const verifyResponse = await fetch('/api/tenant/verify', {
        method: "POST",
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          tenantId: tenantId,
          biometricData: mockBiometricData,
          insuranceProvider: "Seguro General"
        }),
      });

      const verifyResult = await verifyResponse.json();

      if (verifyResult.success && verifyResult.status === 'approved') {
        setVerificationStatus('success');
        setSubmitSuccess("¡Verificación completada! Su perfil está ahora visible para propietarios.");
        setTimeout(() => {
          window.location.href = `/tenant/profile/${tenantId}`;
        }, 3000);
      } else {
        setVerificationStatus('failed');
        setSubmitError(verifyResult.message || "Verificación fallida");
      }

    } catch (error) {
      console.error('Verification error:', error);
      setVerificationStatus('failed');
      setSubmitError(error instanceof Error ? error.message : "Error en la verificación");
    }
  };

  const progressValue = step === 1 ? 50 : step === 2 ? 100 : 0;

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary/5 to-secondary/5 py-12">
      <div className="max-w-2xl mx-auto px-6">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-secondary mb-4">Registro de Inquilino</h1>
          <p className="text-muted-foreground text-lg">
            Complete su registro para acceder a propiedades verificadas
          </p>
          <Progress value={progressValue} className="mt-4 max-w-md mx-auto" />
        </div>

        {step === 1 && (
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl text-center">Información Personal</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
                <div className="grid md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="name">Nombre Completo</Label>
                    <Input 
                      id="name"
                      {...register("name")} 
                      className="mt-1"
                      placeholder="Ej: María González"
                    />
                    {errors.name && <p className="text-red-500 text-sm mt-1">{errors.name.message}</p>}
                  </div>

                  <div>
                    <Label htmlFor="email">Email</Label>
                    <Input 
                      id="email"
                      type="email"
                      {...register("email")} 
                      className="mt-1"
                      placeholder="maria@ejemplo.com"
                    />
                    {errors.email && <p className="text-red-500 text-sm mt-1">{errors.email.message}</p>}
                  </div>
                </div>

                <div className="grid md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="phone">Teléfono</Label>
                    <Input 
                      id="phone"
                      {...register("phone")} 
                      className="mt-1"
                      placeholder="+34 612 345 678"
                    />
                    {errors.phone && <p className="text-red-500 text-sm mt-1">{errors.phone.message}</p>}
                  </div>

                  <div>
                    <Label htmlFor="occupation">Ocupación</Label>
                    <Input 
                      id="occupation"
                      {...register("occupation")} 
                      className="mt-1"
                      placeholder="Ej: Ingeniera de Software"
                    />
                    {errors.occupation && <p className="text-red-500 text-sm mt-1">{errors.occupation.message}</p>}
                  </div>
                </div>

                <div className="grid md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="income">Ingresos Mensuales (€)</Label>
                    <Input 
                      id="income"
                      type="number"
                      {...register("income", { valueAsNumber: true })} 
                      className="mt-1"
                      placeholder="3500"
                    />
                    {errors.income && <p className="text-red-500 text-sm mt-1">{errors.income.message}</p>}
                  </div>

                  <div>
                    <Label htmlFor="maxRent">Alquiler Máximo (€)</Label>
                    <Input 
                      id="maxRent"
                      type="number"
                      {...register("maxRent", { valueAsNumber: true })} 
                      className="mt-1"
                      placeholder="1500"
                    />
                    {errors.maxRent && <p className="text-red-500 text-sm mt-1">{errors.maxRent.message}</p>}
                  </div>
                </div>

                <div>
                  <Label htmlFor="preferredAreas">Zonas Preferidas</Label>
                  <Textarea 
                    id="preferredAreas"
                    {...register("preferredAreas")} 
                    className="mt-1"
                    placeholder="Ej: Centro, Salamanca, Chamberí"
                    rows={3}
                  />
                  {errors.preferredAreas && <p className="text-red-500 text-sm mt-1">{errors.preferredAreas.message}</p>}
                </div>

                <div className="grid md:grid-cols-2 gap-4">
                  <div className="flex items-center space-x-2">
                    <input 
                      type="checkbox" 
                      id="petFriendly"
                      {...register("petFriendly")}
                      className="rounded border-gray-300"
                    />
                    <Label htmlFor="petFriendly">Acepto mascotas</Label>
                  </div>

                  <div className="flex items-center space-x-2">
                    <input 
                      type="checkbox" 
                      id="smokingAllowed"
                      {...register("smokingAllowed")}
                      className="rounded border-gray-300"
                    />
                    <Label htmlFor="smokingAllowed">Permito fumar</Label>
                  </div>
                </div>

                {submitError && (
                  <Alert className="border-red-200 bg-red-50">
                    <AlertDescription className="text-red-800">
                      {submitError}
                    </AlertDescription>
                  </Alert>
                )}

                {submitSuccess && (
                  <Alert className="border-green-200 bg-green-50">
                    <AlertDescription className="text-green-800">
                      {submitSuccess}
                    </AlertDescription>
                  </Alert>
                )}

                <Button 
                  type="submit" 
                  disabled={isSubmitting} 
                  className="w-full bg-primary hover:bg-primary/90 text-secondary font-semibold py-3"
                >
                  {isSubmitting ? "Registrando..." : "Continuar con Verificación"}
                </Button>
              </form>
            </CardContent>
          </Card>
        )}

        {step === 2 && (
          <Card>
            <CardHeader>
              <CardTitle className="text-2xl text-center">Verificación Biométrica</CardTitle>
            </CardHeader>
            <CardContent className="text-center space-y-6">
              {verificationStatus === 'pending' && (
                <>
                  <div className="bg-primary/10 p-8 rounded-lg">
                    <h3 className="text-xl font-semibold mb-4">Proceso de Verificación</h3>
                    <p className="text-muted-foreground mb-6">
                      Para completar su registro, necesitamos verificar su identidad mediante 
                      validación biométrica y confirmación con nuestra aseguradora.
                    </p>
                    <div className="space-y-3 text-left max-w-md mx-auto">
                      <div className="flex items-center space-x-3">
                        <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center text-secondary font-bold text-sm">1</div>
                        <span>Validación biométrica</span>
                      </div>
                      <div className="flex items-center space-x-3">
                        <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center text-secondary font-bold text-sm">2</div>
                        <span>Verificación con aseguradora</span>
                      </div>
                      <div className="flex items-center space-x-3">
                        <div className="w-6 h-6 bg-primary rounded-full flex items-center justify-center text-secondary font-bold text-sm">3</div>
                        <span>Activación de perfil</span>
                      </div>
                    </div>
                  </div>
                  <Button 
                    onClick={handleVerification}
                    className="bg-primary hover:bg-primary/90 text-secondary font-semibold px-8 py-3"
                  >
                    Iniciar Verificación
                  </Button>
                </>
              )}

              {verificationStatus === 'verifying' && (
                <div className="bg-blue-50 p-8 rounded-lg">
                  <div className="animate-spin w-12 h-12 border-4 border-primary border-t-transparent rounded-full mx-auto mb-4"></div>
                  <h3 className="text-xl font-semibold mb-2">Verificando...</h3>
                  <p className="text-muted-foreground">
                    Procesando su información con nuestros sistemas de seguridad.
                    Este proceso puede tomar unos momentos.
                  </p>
                </div>
              )}

              {verificationStatus === 'success' && (
                <div className="bg-green-50 p-8 rounded-lg">
                  <div className="w-16 h-16 bg-green-500 rounded-full flex items-center justify-center mx-auto mb-4">
                    <span className="text-white text-2xl font-bold">✓</span>
                  </div>
                  <h3 className="text-xl font-semibold text-green-800 mb-2">¡Verificación Exitosa!</h3>
                  <p className="text-green-700">
                    Su perfil ha sido verificado y está ahora visible para propietarios.
                    Será redirigido a su perfil en unos segundos.
                  </p>
                </div>
              )}

              {verificationStatus === 'failed' && (
                <div className="bg-red-50 p-8 rounded-lg">
                  <div className="w-16 h-16 bg-red-500 rounded-full flex items-center justify-center mx-auto mb-4">
                    <span className="text-white text-2xl font-bold">✗</span>
                  </div>
                  <h3 className="text-xl font-semibold text-red-800 mb-2">Verificación Fallida</h3>
                  <p className="text-red-700 mb-4">
                    {submitError || "No se pudo completar la verificación. Por favor, inténtelo de nuevo."}
                  </p>
                  <Button 
                    onClick={handleVerification}
                    variant="outline"
                    className="border-red-300 text-red-700 hover:bg-red-50"
                  >
                    Reintentar Verificación
                  </Button>
                </div>
              )}
            </CardContent>
          </Card>
        )}

        <div className="text-center mt-8">
          <Button 
            variant="ghost" 
            onClick={() => window.location.href = '/'}
            className="text-muted-foreground hover:text-secondary"
          >
            Volver al Inicio
          </Button>
        </div>
      </div>
    </div>
  );
}
